/**
 * @file 图书详情
 * @author Yangholmes 2025-03-30
 */

import { computed, defineComponent, PropType, toRefs } from 'vue';
import { JSX } from 'vue/jsx-runtime';
import { I18nT, useI18n } from 'vue-i18n';
import cn from 'classnames';

import Text from '@/components/Text/Text';
import SvgIcon from '@/components/svgIcon/SvgIcon';

import { NRate, NEllipsis, NImage, NTag, NAlert } from 'naive-ui';
import { Book, getBookCover, listRender } from '../utils';

import styles from './bookDetails.module.less';

export default defineComponent({
  name: 'BookDetails',
  props: {
    bookData: {
      type: Object as PropType<Book>,
      default: () => ({})
    },
    locationIcon: {
      type: Object as PropType<JSX.Element>,
      default: () => <SvgIcon name="locationPin" />
    },
    onRateChange: {
      type: Function as PropType<(value: number) => void>
    }
  },
  setup(props) {
    const { bookData, locationIcon } = toRefs(props);

    const { t } = useI18n({ useScope: 'global' });

    const coverUrl = computed(() => {
      const cover = bookData.value?.covers?.[0];
      return getBookCover(cover, bookData.value?.title);
    });

    return () => (<div class={cn(styles['book-details'])}>
      {/* 书名 */}
      <h1>
        {bookData.value?.title}
      </h1>
      {/* ISBN */}
      <p>
        {bookData.value?.isbn}
      </p>
      {/* 作者 */}
      <p>
        {t('pages.book.preview.detail.author')}：
        {listRender(bookData.value?.authors, t('pages.book.preview.detail.noAuthor'), styles.item)}
      </p>
      {/* 评分 */}
      <p>
        <NRate value={bookData.value?.rating} allowHalf onUpdateValue={props.onRateChange} readonly />
      </p>
      {/* 封面 */}
      <p>
        <NImage
          class={cn(styles['book-cover-image'])}
          src={coverUrl.value}
          renderToolbar={({ nodes }) => <>
            {nodes.zoomIn}
            {nodes.resizeToOriginalSize}
            {nodes.zoomOut}
          </>}
        />
      </p>
      {/* 位置 */}
      <p>
        <NAlert showIcon={false} type="info">
          <I18nT scope="global" keypath="pages.book.preview.detail.location">
            <NTag>{locationIcon.value}</NTag>
          </I18nT>
        </NAlert>
      </p>

      <h3>{t('pages.book.preview.detail.details')}</h3>
      {/* 出版社 */}
      <p>
        {listRender(bookData.value?.publishers, t('pages.book.preview.detail.noPublisher'), styles.item)}
      </p>
      {/* 出版时间 */}
      <p>
        出版时间： <Text text={bookData.value?.publishDate} />
      </p>
      {/* 语言 */}
      <p>
        {listRender(bookData.value?.languages, t('pages.book.preview.detail.noLanguage'), styles.item)}
      </p>
      {/* 总页数 */}
      <p>
        <I18nT scope="global" keypath="pages.book.preview.detail.numberOfPages.text">
          <Text text={bookData.value?.numberOfPages} placeholder={t('pages.book.preview.detail.numberOfPages.placeholder')} />
        </I18nT>
      </p>

      {/* 标签 */}
      <p>
        {listRender(bookData.value?.tags, t('pages.book.preview.detail.noTags'), styles.item)}
      </p>
      {/* 分类 */}
      <p>
        {listRender(bookData.value?.subjects, t('pages.book.preview.detail.noSubject'), styles.item)}
      </p>

      {/* 简介 */}
      <p>
        <h3>{t('pages.book.preview.detail.summary')}</h3>
        <div>
          <NEllipsis
            expandTrigger="click"
            lineClamp={5}
            tooltip={false}
          >
            <Text text={bookData.value?.summary} />
          </NEllipsis>
        </div>
      </p>

      {/* 书架信息 */}
      {
        // TODO
      }
    </div>);
  }
});

