/**
 * @file 书籍位置分布
 * @author Yangholmes 2025-04-19
 */

import { defineComponent, PropType, ref, watch } from 'vue';
import cn from 'classnames';
import useTRouter from '@/composite/useTRouter';

import BookcasePreview from '@/pages/bookcase/bookcasePreview/BookcasePreview';

import styles from './bookLocation.module.less';

export default defineComponent({
  name: 'BookLocation',
  props: {
    bookcaseIds: {
      type: Array as PropType<{
        bookcaseId?: string;
        cellId?: string;
      }[]>,
      default: () => []
    }
  },
  setup(props) {
    const { router } = useTRouter();
    const heights = ref<number[]>([]);
    const isLoaded = ref<boolean>(false);

    const positions = ref<[number, number][]>([]);
    const height = ref<number>(0);

    watch(heights.value, (newH) => {
      if (newH.filter(item => item !== undefined).length === props.bookcaseIds.length) {
        isLoaded.value = true;
        const p: [number, number][] = [];
        const maxY = [0, 0];
        for (let i = 0; i < newH.length; i++) {
          if (maxY[0] <= maxY[1]) {
            p.push([0, maxY[0]]);
            maxY[0] += newH[i];
          } else {
            p.push([1, maxY[1]]);
            maxY[1] += newH[i];
          }
        }
        positions.value = p;
        height.value = Math.max(...maxY);
      }
    });

    return () => (<div
      class={cn(styles['book-location'])}
      style={{ height: `${height.value + 1}px` }}
    >
      {props.bookcaseIds.map((bookcase, index) => {
        const position = positions.value[index] || [0, 0];
        const x = position[0] === 0 ? '0%' : '100%';
        const y = position[1] + 'px';
        return (
          <div
            class={cn(styles['preview-card'])}
            style={{
              visibility: isLoaded.value ? 'visible' : 'hidden',
              transform: `translate(${x}, ${y})`
            }}
            onClick={() => {
              router.push(`/cell/${bookcase.cellId}`);
            }}
          >
            <BookcasePreview
              key={bookcase.bookcaseId}
              bookcaseId={bookcase.bookcaseId}
              selectedCellId={bookcase.cellId}
              onSizeLoad={(_, height) => {
                heights.value[index] = height;
              }}
              isLocated={index === 0}
            />
          </div>
        );
      })}
    </div>);
  }
});
