/**
 * @file 重复图书图标
 * @author Yangholmes 2025-04-19
 */

import { computed, defineComponent, nextTick, ref, toRefs } from 'vue';

import cn from 'classnames';

import { getBookCover } from '../utils';

import styles from './duplicateBooksIcon.module.less';

export default defineComponent({
  name: 'DuplicateBooksIcon',
  props: {
    cover: {
      type: Blob,
    },
    title: {
      type: String,
    }
  },
  setup(props) {
    const { cover, title } = toRefs(props);

    const coverUrl = computed(() => {
      return getBookCover(cover.value, title.value);
    });

    const flip = ref(false);

    setTimeout(() => {
      nextTick(() => {
        flip.value = true;
      });
    }, 1e3);

    return () => (
      <div
        class={cn(styles['duplicate-books-icon'], {
          [styles.flip]: flip.value
        })}
        style={{
          ['--background-image']: `url(${coverUrl.value})`,
        }}
      >{'⬜️'}</div>
    );
  }
});
