/**
 * @file 预览书籍
 * @author Yangholmes 2025-03-21
 */

import { defineComponent, ref, watch } from 'vue';
import cn from 'classnames';
import { useI18n } from 'vue-i18n';
import useTRouter from '@/composite/useTRouter';

import BookDetails from './BookDetails';
import BasicTopBar from '@/pages/layout/BasicTopBar';
import BasicLayout from '@/pages/layout/BasicLayout';
import DuplicateBooksIcon from './DuplicateBooksIcon';
import DoubleDeckerContainer from '@/components/doubleDeckerContainer/DoubleDeckerContainer';
import DeckerLayout from '@/pages/layout/DeckerLayout';
import BookLocation from './BookLocation';
import SvgIcon from '@/components/svgIcon/SvgIcon';

import db from '@/services/db';
import { BookInCell, BookInCellRawCover } from '../utils';

import { NButton } from 'naive-ui';

import styles from './previewBook.module.less';

export default defineComponent({
  name: 'PreviewBook',
  setup() {
    const { t } = useI18n({ useScope: 'global' });
    const { route, router } = useTRouter();

    const bookData = ref<BookInCell>();

    // 相同 isbn 重复的书籍
    const duplicateBooks = ref<BookInCellRawCover[]>([]);

    watch(() => route.params, (params) => {
      const { isbn, id } = params as any;

      if (id) {
        db.book.queryRelatedBooksByBookId(id).then((res) => {
          if (res?.length) {
            const [book, ...restBooks] = res;
            bookData.value = book;
            duplicateBooks.value = restBooks || [];
          }
        });
      } else if (isbn) {
        db.book.queryFirstBookByIsbn(isbn).then((res) => {
          if (res?.length) {
            const [book, ...restBooks] = res;
            bookData.value = book;
            duplicateBooks.value = restBooks || [];
          }
        });
      }
    }, {
      immediate: true
    });

    const renderLocationIcon = () => {
      return duplicateBooks.value.length
        ? <DuplicateBooksIcon cover={bookData.value?.covers?.[0]} title={bookData.value?.title} />
        : <SvgIcon name="locationPin" />;
    };

    function onEdit() {
      const id = bookData.value?.id;
      id && router.push({
        path: `/book/edit/id/${id}`,
        replace: true
      });
    }

    const renderDefault = (go: () => void) => <div class={cn(styles['preview-book'])}>
      <BasicLayout topBar={
        (<BasicTopBar
          rightSlots={[
            <NButton onClick={go}>
              {renderLocationIcon()}
            </NButton>
            ,
            <NButton onClick={onEdit}>
              {/* ✏️ */}
              <SvgIcon name="pencil" />
            </NButton>
          ]}
        >
          {bookData.value?.title || '-'}
        </BasicTopBar>)
      }>
        <BookDetails
          bookData={bookData.value}
          locationIcon={renderLocationIcon()}
        />
      </BasicLayout>
    </div>;

    const renderDecker = (back: () => void) => {
      const bookcaseIds = [
        bookData.value,
        ...duplicateBooks.value
      ].filter(book => !!book).map((book) => ({
        bookcaseId: book.bookcaseId,
        cellId: book.cellId
      }));
      return <DeckerLayout
        topBar={<BasicTopBar
          leftSlots={[<NButton
            text
            onClick={back}
          >
            {t('common.close')}
          </NButton>]}
        >
          {t('pages.book.preview.decker.topbar.title')}
        </BasicTopBar>}
      >
        <BookLocation bookcaseIds={bookcaseIds} />
      </DeckerLayout>;
    };

    return () => <DoubleDeckerContainer>
      {{
        default: renderDefault,
        decker: renderDecker
      }}
    </DoubleDeckerContainer>;
  }
});

